<template>
  <div style="margin-bottom: 60px">
    <van-sticky>
      <div class="topTab">
        <van-icon name="https://b.yzcdn.cn/vant/icon-demo-1126.png" size="30" />
        <van-tabs v-model="active" background="transparent" animated>
          <van-tab v-for="(item, index) in ['关注', '推荐']" :key="index" :title="item">
          </van-tab>
        </van-tabs>
        <van-icon name="bullhorn-o" size="30" />
      </div>
    </van-sticky>

    <!-- 米圈内容 -->
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad" v-show="active === 1">
      <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
      <div class="ListItem" v-for="(item, index) in miList" :key="index" @click="toDetail(item.content.id)">
        <div class="userInfo">
          <img :src="item.content?.icon" alt="" />
          <div class="username">{{ item.content?.nickname }}</div>
        </div>
        <div class="content">{{ item.content?.content }}</div>
        <div class="showImg">
          <img v-for="(img, index) in item.content?.pic_list" :key="index" :src="img.img" alt="" />
        </div>
        <!-- 图标 -->
        <div class="btnIcon">
          <div><van-icon name="share-o" size="20" /></div>
          <div style="display: flex; align-items: center">
            <van-icon name="chat-o" size="20" />
            <span>{{ item?.content.comment_num }}</span>
          </div>
          <div style="display: flex; align-items: center">
            <van-icon name="good-job-o" size="20" /><span>{{
              item?.content.praise_num
              }}</span>
          </div>
        </div>
      </div>
    </van-list>
    <div v-show="active === 0">
      <van-empty description="暂无关注" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 1,
      miList: null,
      // 触底
      loading: false,
      finished: false,
    };
  },
  methods: {
    getMIList() {
      this.axios.get("communicate/home_rec?size=10").then((res) => {
        console.log(res.data.data.rec_list);
        this.miList = res.data.data.rec_list;
      });
    },
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        if (this.miList) {
          this.miList = [...this.miList, ...this.miList]
        }

        // 加载状态结束
        this.loading = false;

        // // 数据全部加载完成
        // if (this.list.length >= 40) {
        // this.finished = true;
        // }
      }, 1000);
    },
    toDetail(id){
      if(id !== 0){
        this.$router.push({ path: '/micomment',query:{id:id}})
      }
    }
  },
  activated() {
    this.getMIList();
  },
};
</script>

